body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-Header {
  width: 100%;
  background-color: #282c34;
  height: 350px;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-My-Des{
  font-size: 14px;
  color: #f92372;
}

.App-Content{
  width: 580px;
  margin: 0 auto;
  padding-top: 10px;
}

.App-Content a{
  color: #0084FF;
}
.header-container{
  width: 100%;
  height: 52px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.header-title {
  padding-left: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.header-title a{
  text-decoration: none;
  color: #000;
}

.header-title img {
  width: 30px;
  margin-right: 5px;
}

.header-nav-link{
  flex: 1 1;
  padding-right: 10px;
  text-align: right;
}

.header-nav-link a{
  margin-right: 10px;
  text-decoration: none;
  color: rgba(0,0,0,.5);
}

.header-nav-link a:hover{
  color: rgba(0,0,0,.7);
}
